.orgchart {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: rgba(0, 0, 0, 0);
  /* default - transparent */
  background-size: 10px 10px;
  padding: 20px;
  overflow: auto;
  /* prevent child element from spilling outside */
  margin: 0 auto;
  border: 1px solid lightgrey;
  width: 100%;
  max-height: 59vh;
}

.orgchart .hidden, .orgchart ~ .hidden {
  display: none !important;
}

.orgchart *, .orgchart *:before, .orgchart *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}

.orgchart.b2t {
  transform: rotate(180deg);
  -ms-transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
}

.orgchart.l2r {
  position: absolute;
  transform: rotate(-90deg) rotateY(180deg);
  -ms-transform: rotate(-90deg) rotateY(180deg);
  -moz-transform: rotate(-90deg) rotateY(180deg);
  -webkit-transform: rotate(-90deg) rotateY(180deg);
  transform-origin: left top;
  -ms-transform-origin: left top;
  -moz-transform-origin: left top;
  -webkit-transform-origin: left top;
}

.orgchart .verticalNodes ul {
  list-style: none;
  margin: 0px;
  padding-left: 18px;
  text-align: left;
}

.orgchart .verticalNodes ul:first-child {
  margin-top: 2px;
}

.orgchart .verticalNodes > td::before {
  content: "";
  border: 2px solid #C75B12;
}

.orgchart .verticalNodes > td > ul > li:first-child::before {
  top: -4px;
  height: 30px;
  width: calc(50% - 2px);
  border-width: 4px 0 0 4px;
}

.orgchart .verticalNodes ul > li {
  position: relative;
  margin: 0;
}

.orgchart .verticalNodes ul > li::before,
.orgchart .verticalNodes ul > li::after {
  content: "";
  position: absolute;
  left: -6px;
  border-color: #C75B12;
  border-style: solid;
  border-width: 0 0 4px 4px;
}

.orgchart .verticalNodes ul > li::before {
  z-index: 0;
}

.orgchart .verticalNodes ul > li::before {
  top: -4px;
  height: 30px;
  width: 11px;
}

.orgchart .verticalNodes ul > li::after {
  top: 1px;
  height: 100%;
}

.orgchart .verticalNodes ul > li:first-child::after {
  top: 24px;
  width: 11px;
  border-width: 4px 0 0 4px;
}

.orgchart .verticalNodes ul > li:last-child::after {
  border-width: 4px 0 0;
}

.orgchart.r2l {
  position: absolute;
  transform: rotate(90deg);
  -ms-transform: rotate(90deg);
  -moz-transform: rotate(90deg);
  -webkit-transform: rotate(90deg);
  transform-origin: left top;
  -ms-transform-origin: left top;
  -moz-transform-origin: left top;
  -webkit-transform-origin: left top;
}

.orgchart > .spinner {
  font-size: 100px;
  margin-top: 30px;
  color: rgba(68, 157, 68, 0.8);
}

.orgchart table {
  border-spacing: 0 !important;
  border-collapse: separate !important;
  border: none;
  width: auto;
  table-layout: fixed;
}

.orgchart > table:first-child {
  margin: 0px auto;
}

.orgchart > table > tbody {
  margin: 20px;
  padding: 15px;
}

.orgchart td {
  text-align: center;
  vertical-align: top;
  padding: 0;
  border: none;
  line-height: 1.3;
}

.orgchart tr.lines td.topLine {
  border-top: 4px solid #C75B12;
}

.orgchart tr.lines td.rightLine {
  border-right: 2px solid #C75B12;
  float: none;
  border-radius: 0px;
}

.orgchart tr.lines td.leftLine {
