  margin: 0px auto;
  height: 20px;
  width: 4px;
  /* double - rightLine+leftLine */
  float: none;
}

/* node styling */
.orgchart .node {
  display: inline-block;
  position: relative;
  margin: 0;
  padding: 3px;
  border: 2px dashed transparent;
  text-align: center;
  width: 140px;
}

.orgchart.l2r .node, .orgchart.r2l .node {
  width: 50px;
  height: 130px;
}

.orgchart .node > .spinner {
  position: absolute;
  top: calc(50% - 15px);
  left: calc(50% - 15px);
  vertical-align: middle;
  font-size: 30px;
  color: rgba(68, 157, 68, 0.8);
}

.orgchart .node:hover {
  background-color: rgba(199, 91, 18, 0.5);
  transition: 0.5s;
  cursor: default;
  z-index: 20;
}

.orgchart .node.focused {
  background-color: rgba(199, 91, 18, 0.8);
}

.orgchart .ghost-node {
  position: fixed;
  left: -10000px;
  top: -10000px;
}

.orgchart .ghost-node > * {
  fill: #ffffff;
  stroke: #bf0000;
}

.orgchart .node.allowedDrop {
  border-color: rgba(68, 157, 68, 0.9);
}

/* Top half of node - contains title - SM */
.orgchart .node .title {
  text-align: center;
  font-size: 12px;
  height: auto;
  line-height: 20px;
  overflow: visible;
  text-overflow: ellipsis;
  white-space: pre-line;
  background-color: #f5f5f5;
  color: 0;
  border: 1px solid #B7B1A9;
  border-radius: 4px 4px 0 0;
  padding-left: 10px;
  padding-right: 10px;
}

.orgchart.b2t .node .title {
  transform: rotate(-180deg);
